<template>
  <div>
    <button @click="startShow">开启弹窗</button>
    <button @click="closeShow">关闭弹窗</button>
    <transition
      enter-active-class="animate__animated animate__backInUp"
      leave-active-class="animate__animated animate__backOutUp"
    >
      <div id="container" v-show="show">{{ showItem.project}}已延期,担当为{{ showItem.name }}</div>
    </transition>
  </div>
</template>

<script>
import 'animate.css'
export default {
  name:'msgBox',
  data() {
    return {
      show: false,
      showItem:{project:'',name:''}
    }
  },
  props:['lateArr'],
  methods: {
    startShow(){
      clearInterval(this.timer)
      let flag=true
      let index=-1
      this.timer=setInterval(()=>{
        if(flag){
            index++
        }
        if(index===this.lateArr.length){
            index=0
        }
        flag=!flag
        this.show=!this.show
        this.showItem=this.lateArr[index]
      },3000)
    },
    closeShow(){
      clearInterval(this.timer)
      this.show=false
    }
  }
}
</script>

<style scoped>
  #container{
    position:fixed;
    top:300px;
    left:30%;
    height:300px;
    width:40%;
    background-color:rgb(221, 119, 119);
    line-height: 300px;
    text-align: center;
    font-size:200%;
  }
</style>