import Vue from 'vue'
import VueRouter from "vue-router";
Vue.use(VueRouter)
import AcceptPage from '../components/AcceptPage'
import OrderPage from '../components/OrderPage'

export default new VueRouter({
  routes:[
    {
      name:'order',
      path:'/order',
      component:OrderPage
    },
    {
      name:'accept',
      path:'/accept',
      component:AcceptPage
    }
  ]
})