<template>
  <div>
    <msgBox id="msg" :lateArr="lateData"></msgBox>
    <div id="link">
      <router-link active-class="active" :to="{
        name:'order',
        params:{
          datas
        }
      }">发注状况</router-link>
      <span> | </span>
      <router-link active-class="active" :to="{
        name:'accept',
        params:{
          datas
        }
      }">验收状况</router-link>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import axios from 'axios'
import msgBox from './components/msgBox.vue'
export default{
  name: 'App',
  data(){
    return{
      datas:[],
      lateData:[]
    }
  },
  components:{msgBox},
  methods:{
    getData(){
      axios.get('http://127.0.0.1:5001/test').then(
        response=>{
          this.datas=response.data//datas传值给两个page组件
        },
        error=>{
          console.log(error.message)
        }
      )
    },
    getLateData(){
      axios.get('http://127.0.0.1:5001/late').then(
        response=>{
          this.lateData=response.data
        },
        error=>{
          console.log(error.message)
        }
      )
    }
  },
  mounted(){
    this.getData();
    this.getLateData()
  }

}
</script>
<style scoped>
  #link{
    text-align: center;
  }
  #link a{
    text-decoration: none;
  }
  .active{
    color:red
  }
  #msg{
    position:fixed;
    z-index: 999;
  }
</style>
