<template>
  <div>
    <div id="container">
      <theList id="yellow" :data="yellowData" :color="yellow" :title="title[0]" :isShow="isShow"></theList>
      <theList id="green" :data="greenData" :color="green" :title="title[1]" :isShow="isShow"></theList>
    </div>
  </div>
</template>

<script>
import theList from './theList.vue'
import dayjs from 'dayjs'
export default{
  name: 'AcceptPage',
  data(){
    return{
      yellowData:[],
      greenData:[],
      isShow:false,
      yellow:['yellow','rgb(255, 255, 127)','rgb(255, 255, 187)'],
      green:['green','rgb(156, 204, 141)','rgb(212, 245, 205)'],
      title:['本月应验收','流程进行中']
    }
  },
  components:{theList },
  methods:{
    forAccept(){
      this.yellowData=[]
      this.greenData=[]
      const monthNow=dayjs(Date.now()).format('YYYY年MM月')
      this.$route.params.datas.forEach((item)=>{
        if(item.accept===monthNow){
          this.yellowData.push(item)
        }
        if(item.accept>monthNow){
          this.greenData.push(item)
        }
      })
      this.greenData.sort((a,b)=>{
        return Number(a.accept.substring(0,4)+a.accept.substring(5,7))-Number(b.accept.substring(0,4)+b.accept.substring(5,7))
      })

    }
  },
  mounted(){
    this.forAccept()
  }
}
</script>
<style scoped>
  #container{
    width:100%;
    height:1200px;
    overflow: hidden;
  }
  #yellow{
    width:49.9%;
    float:left
  }
  #green{
    width:49.9%;
    float:right;
  }
</style>
